import React from 'react'
import ReactDOM from 'react-dom'
import './index.css'

/* 

id=" 0 - 9 , equals , add, subtract, multiply, divide,"
. id="decimal" adds . at the current state,
clear button = state : 0,
has a id="display",

IF MULTIPLE 5 + * 7 = 5 * 7, the last operator takes priority,
BUT IF 5 * - 5 = 5*(-5), subtract is excluded,

parseInt(), will be useful here
*/

/*
        for(let i = 0; i < string.length; i++){
           if(string[i] === "0" || string[i] ==="1" || string[i] ==="2" || string[i] === "3"
                || string[i] ==="4" || string[i] === "5" || string[i] === "6" || string[i] === "7" 
                    || string[i] === "8" || string[i] === "9") {
                        numbers.push(parseInt(string[i]))
            } else {
                if(string[i] === "+" || string[i] === "-"){
                    numbers.push(string[i])
                    }else if (string[i] === "×"){
                        numbers.push('*')
                    }else{
                        numbers.push('/')
                    }
            }
        }
        */

class App extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            total : 0,

        }
        this.handleClick = this.handleClick.bind(this)
        this.handleClear = this.handleClear.bind(this)
        this.handleEqual = this.handleEqual.bind(this)
        this.handleDemical = this.handleDemical.bind(this)
        this.handleZero = this.handleZero.bind(this)
    }

    handleClick(event){
        if(this.state.total === 0){
            this.setState({
                total : event.target.innerText
            })
        }else{
            this.setState({
                total : this.state.total + event.target.innerText
            })
        }
        console.log('clicked')
    }

    handleClear(){
        this.setState({
            total : 0
        })
    }

    handleZero(event){
        if(this.state.total === 0){
            this.setState({
                total : 0
            })
        }else{
        this.setState({
            total : this.state.total + event.target.innerText
            })
        }
        
        event.preventDefault()
    }

    handleEqual(){
        let numbers = this.state.total.slice().split(/[^0-9|.]/ig).filter(item => item !== "")
        let operators = this.state.total.slice().split(/[0-9|.]/ig).filter(item => item !== "")
        if(this.state.total.charAt(0) === '-'){
            numbers[0] = '-'.concat(numbers[0])
            operators.shift()
        }
        console.log(numbers)
        numbers = numbers.map(num => parseFloat(num))
        for( let z = 0; z < operators.length; z++){
            if(/\+$/.test(operators[z])){
                operators[z] = "+"
            } else if(/×$/.test(operators[z])){
                operators[z] = "*"
            } else if(/-$/.test(operators[z])){
                if(operators[z] === "-"){  
                    operators[z] = "-"
                }else if(/×-$/.test(operators[z])){
                    operators[z] = "*-"
                }else if(/--$/.test(operators[z])){
                    operators[z] = "-"
                }else if(/\+-$/.test(operators[z])){
                    operators[z] = "-"
                }else {
                    operators[z] = "/-"
                }
            }  
            else if(operators[z] === "×"){
                operators[z] = "*"
            }else{
                operators[z] = "/"
            }
        }

        let wholeop = numbers.slice();
        let lengthofOp = 0;
        let odd = 1;
        while(lengthofOp !== operators.length){
            wholeop.splice(odd,0,operators[lengthofOp])
            lengthofOp++
            odd += 2
        }
  
        while(wholeop.length !== 1){
            if(wholeop.includes('/-')){
                wholeop.splice(wholeop.indexOf("/-")-1,3, wholeop[wholeop.indexOf("/-")-1]/(-wholeop[wholeop.indexOf("/-")+1]))
        }else if(wholeop.includes('*-')){
                wholeop.splice(wholeop.indexOf("*-")-1,3, wholeop[wholeop.indexOf("*-")-1]*(-wholeop[wholeop.indexOf("*-")+1]))
        }else if(wholeop.includes('*')){
            if(wholeop.indexOf('/') === -1){
                wholeop.splice(wholeop.indexOf("*")-1,3, wholeop[wholeop.indexOf("*")-1]*(wholeop[wholeop.indexOf("*")+1]))
            }else if(wholeop.indexOf('/') < wholeop.indexOf('*')){
                wholeop.splice(wholeop.indexOf("/")-1,3, wholeop[wholeop.indexOf("/")-1]/(wholeop[wholeop.indexOf("/")+1]))
            }else{
                wholeop.splice(wholeop.indexOf("*")-1,3, wholeop[wholeop.indexOf("*")-1]*(wholeop[wholeop.indexOf("*")+1]))
            }
        }else if(wholeop.includes('/')){
            wholeop.splice(wholeop.indexOf("/")-1,3, wholeop[wholeop.indexOf("/")-1]/(wholeop[wholeop.indexOf("/")+1]))
        }else if(wholeop.includes('-')){
            wholeop.splice(wholeop.indexOf("-")-1,3, wholeop[wholeop.indexOf("-")-1]-wholeop[wholeop.indexOf("-")+1])
        }else if(wholeop.includes('+')){
            wholeop.splice(wholeop.indexOf("+")-1,3, wholeop[wholeop.indexOf("+")-1]+wholeop[wholeop.indexOf("+")+1])
        }

        this.setState({
            total : wholeop[0]
        })
        console.log(numbers)
        console.log(operators)
        console.log('output')
        console.log(wholeop)
        }
    }

    handleDemical(event){
        if(this.state.total.toString().charAt(this.state.total.length-1) !== '.' && 
            this.state.total.toString().charAt(this.state.total.length-2) !== '.' && 
                this.state.total.toString().charAt(this.state.total.length-3) !== '.'){
            console.log(this.state.total.toString().charAt(this.state.total.length-2))
            console.log(this.state.total.toString().charAt(this.state.total.length-3))       
            this.setState({
                total : this.state.total + event.target.innerText
            })
        }
        event.preventDefault()
    }
    render(){
        return(
            <div id="afterRoot">
                    <h1 id="title">Jun's Calculator</h1>
                <div id="calcdiv">
                    <div id="display">
                        <h1 style={{letterSpacing: 3,textAlign : 'right', paddingRight : 10}}>{this.state.total}</h1>
                    </div>
                <div id="click">
                    <button onClick={this.handleClear}id="clear">C</button>
                    {/* empty divs for spacing out*/}
                    <div></div>
                    <div></div>
                    <div></div>
                    <button onClick={this.handleClick} id="seven">7</button>
                    <button onClick={this.handleClick} id="eight">8</button>
                    <button onClick={this.handleClick} id="nine">9</button>
                    <button onClick={this.handleClick} id="divide" style={{fontSize : 36}}>&divide;</button>
                    <button onClick={this.handleClick} id="four">4</button>
                    <button onClick={this.handleClick} id="five">5</button>
                    <button onClick={this.handleClick} id="six">6</button>
                    <button onClick={this.handleClick} id="multiply" style={{fontSize : 36}}>&times;</button>
                    <button onClick={this.handleClick} id="one">1</button>
                    <button onClick={this.handleClick} id="two">2</button>
                    <button onClick={this.handleClick} id="three">3</button>
                    <button onClick={this.handleClick} id="subtract" style={{fontSize : 36}}>-</button>
                    <button onClick={this.handleZero} id="zero">0</button>
                    <button onClick={this.handleDemical} id="decimal" style={{fontSize : 36}}>.</button>
                    <button onClick={this.handleEqual} id="equals" style={{fontSize : 36}}>=</button>
                    <button onClick={this.handleClick} id="add" style={{fontSize : 36}}>+</button>
                </div>
                </div>
            </div>
        )
    }
}


ReactDOM.render(<App />, document.getElementById('root'))